* {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  &, &::before, &::after {
    -webkit-box-sizing: border-box;
    -khtml-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box; } }

 :root {
  // pegs to 1rem = 10px
  font-size: 62.5%;
  body {
    margin: 0;
    // Body font size 16px
    font-size: 1.6rem; } }

::selection {
  background-color: #db4031 !important;
  color: #fff; }

html, body {
  position: relative;
  min-height: 100%;
  width: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
  color: #32343d;
  background-color: #fff;
  font-weight: 500;
  font-family: 'Montserrat', Helvetica, 'Arial CE', Arial, sans-serif;
  line-height: 1.3;
  -webkit-font-smoothing: antialiased; }


.columns {
  .is-vcentered {
    flex-wrap: wrap;
    // use this for multiple child
    align-content: center;
    // use if an only child
    align-items: center; } }

