a {
  &, &:focus, &:hover, &:active {
    @include transition(0.3s, 'color', 'border', 'border-color');

    color: inherit;
    text-decoration: none; }

  &.proper-a {
    color: #118fb6;
    border-bottom: 1px dotted #118fb6;

    &:hover, &:active {
      color: #0c7393;
      border-bottom: 1px solid #0e7d9f; } } }


.container {
  $pad-m: 1rem;
  $pad-l: 2rem;
  padding: {
    left: $pad-m;
    right: $pad-m; }

  @include breakpoint($desktop) {
    padding: {
      left: $pad-l;
      right: $pad-l; } } }
