@import '../common/_all';
@import '../colors/_all';

#body {
  &-side {
    @include breakpoint(0 $desktop) {
      display: none; }

    position: fixed;
    height: auto;
    width: 100vw;
    padding-top: $mnav-height-desktop;
    background-color: #fff;
    z-index: 1000;

    @include breakpoint($desktop) {
      height: 100vh;
      width: $bside-width-desktop;
      border-right: 1px solid #e5e5e5; } }

  &-main {
    position: relative;
    width: 100vw;
    padding-top: 6.8rem;
    background-color: #f5f5f8;
    z-index: 1;

    @include breakpoint($desktop) {
      padding: {
        top: $mnav-height-desktop;
        left: $bside-width-desktop; } } } }

.bside {
  position: fixed;

  & .container {
    padding: 0; }

  &-block {
    padding: 1rem 0; }

  &-nav {
    padding: 1rem 0;

    &:last-of-type {
      padding-bottom: 0; }

    &-hline {
      border-top: 1px solid #eee; }

    li {
      &.nav-title {
        font-weight: bold;

        i {
          color: inherit; }

        a {
          cursor: default;

          &:hover, a:active {
            background-color: inherit;
            border-left-color: transparent; } } }

      a {
        display: block;
        border-left: 5px solid transparent;

        @include breakpoint($desktop) {
          padding: 0.8rem 0.5rem; }

        i {
          color: $punch; }

        &:hover, &:active {
          background-color: #e5e5e5;
          border-left-color: $punch; } }

      i {
        width: 4rem;
        padding: 0 0 0 1rem; }

      &:not(.with-icon) {
        a {
          @include breakpoint($desktop) {
            padding {
              right: 2rem;
              left: 2rem; } } } } } } }
