// mobile: ~ 768px
// tablet: 769px ~ 1023px
// desktop: 1024px ~ 1215px
// widescreen: 1216px ~ 1407px
// full hd: 1408px ~
$mobile: 768px;
$desktop: 1024px;
$xdesktop: 1440px;
$xldesktop: 1900px;

$mnav-height-desktop: 70px;
$bside-width-desktop: 20rem;

// padding
$pad-top-main: 3.2rem;

// ts: transition
$ts-cubic: cubic-bezier(0.6, 0.1, 0.3, 0.8);

// font awesome
// path is relative to the compiled directory, i.e. dist/
// $fa-font-path: '../../webfonts'

$bdr1: 4px;
$bdr2: 8px;

