#wrapper {
  width: 100%;
  padding: 1.5rem 0;

  @include breakpoint($desktop) {
    padding: 2.5rem 0; } }

.help-us {
  position: absolute;
  top: 0.6rem;
  right: 5rem;
  text-align: center;

  & a {
    display: block;
    width: 8rem;
    padding: {
      top: 0.4rem;
      bottom: 0.4rem; }
    color: #fff;
    background-color: $green;
    border-radius: $bdr1;

    &:hover, &:active {
      background-color: $jungle-green; } }

  @include breakpoint($desktop) {
    display: none; } }

